// component/blogService/PostList.tsx
'use client';

import { useEffect, useState, useRef, useCallback } from 'react';
import PostCard from './PostCard';
import styles from "@/styles/blogService/post.module.css";
import { getTrendingPosts, getRecentPosts } from "@/api/blogService/blog";
import { PostEntity } from "@/types/blogService/blogType";
const PAGE_SIZE = 10;

// Prop ÌÉÄÏûÖ Ï†ïÏùò Ïú†ÏßÄ
interface PostListProps {
  postType: 'trending' | 'recent';
}

export default function PostList({ postType }: PostListProps) {
  const [posts, setPosts] = useState<PostEntity[]>([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(0);
  const [hasMore, setHasMore] = useState(true);

  const loaderRef = useRef<HTMLDivElement | null>(null);

  // pageRefÏôÄ hasMoreRefÎäî ÌòÑÏû¨ Î°úÏßÅÏóêÏÑú Ïûò ÏÇ¨Ïö©ÎêòÍ≥† ÏûàÏúºÎØÄÎ°ú Ïú†ÏßÄÌï©ÎãàÎã§.
  const pageRef = useRef(0);
  pageRef.current = page;
  const hasMoreRef = useRef(true);
  hasMoreRef.current = hasMore;


  // loadPosts Ìï®ÏàòÎäî useCallback ÏùòÏ°¥ÏÑ± Î∞∞Ïó¥Ïóê postTypeÏùÑ Ï∂îÍ∞ÄÌïòÏó¨,
  // ÌÉ≠ Î≥ÄÍ≤Ω Ïãú Ïò¨Î∞îÎ•∏ postTypeÏúºÎ°ú Î°úÎìúÎêòÎèÑÎ°ù Î≥¥Ïû•Ìï©ÎãàÎã§.
  const loadPosts = useCallback(async (pageToLoad: number, currentPostType: 'trending' | 'recent') => {
    // ... (Í∏∞Ï°¥ loadPosts Î°úÏßÅ Ïú†ÏßÄ)
    setLoading(true);

    try {
      const fetchFunction = currentPostType === 'trending' ? getTrendingPosts : getRecentPosts;

      const res = await fetchFunction(pageToLoad, PAGE_SIZE);

      if (res?.content) {
        setPosts(prevPosts =>
            pageToLoad === 0 ? res.content! : [...prevPosts, ...res.content!]
        );

        setHasMore(pageToLoad < res.totalPages - 1);

      } else {
        setHasMore(false);
      }
    } catch (error) {
      console.error(`Failed to fetch ${currentPostType} posts:`, error);
      setHasMore(false);
    } finally {
      setLoading(false);
    }
  }, []); // ‚úÖ ÏùòÏ°¥ÏÑ± Î∞∞Ïó¥Ïóê ÏïÑÎ¨¥Í≤ÉÎèÑ ÏóÜÏúºÎ©¥ currentPostType Î≥ÄÍ≤Ω Ïãú ÏÉà Ìï®ÏàòÍ∞Ä ÏÉùÏÑ±ÎêòÏßÄ ÏïäÏùå.
          // Î°úÏßÅÏù¥ postTypeÏùÑ Ïù∏ÏûêÎ°ú Î∞õÏúºÎØÄÎ°ú, Ïù¥ Î∂ÄÎ∂ÑÏùÑ Îπà Î∞∞Ïó¥Î°ú ÎëêÎäî Í≤ÉÏùÄ Ïú†ÏßÄÌï¥ÎèÑ Í¥úÏ∞ÆÏäµÎãàÎã§.


  // --- 1. postTypeÏù¥ Î≥ÄÍ≤ΩÎê† Îïå ÏÉÅÌÉú Ï¥àÍ∏∞Ìôî Î∞è Ï≤´ ÌéòÏù¥ÏßÄ Î°úÎìú (Ïú†ÏßÄ) ---
  useEffect(() => {
    // ... (Í∏∞Ï°¥ Î°úÏßÅ Ïú†ÏßÄ)
    setPosts([]);
    setPage(0);
    setHasMore(true);
    setLoading(false);
    loadPosts(0, postType);
  }, [postType, loadPosts]);


  // --- 2. Intersection Observer ÏÑ§Ï†ï (Î°úÏßÅ ÏàòÏ†ï ÏóÜÏù¥ Ïú†ÏßÄ) ---
  // ObserverÏùò ÏΩúÎ∞± Ìï®ÏàòÎäî ÏµúÏã† hasMoreRef.currentÎ•º ÏÇ¨Ïö©ÌïòÎØÄÎ°ú, Î°úÏßÅÏùÄ Ïú†Ìö®Ìï©ÎãàÎã§.
  useEffect(() => {
    const observer = new IntersectionObserver(
        (entries) => {
          const target = entries[0];
          // Î°úÎçîÍ∞Ä Î≥¥Ïù¥Í≥†, Î°úÎî© Ï§ëÏù¥ ÏïÑÎãàÎ©∞, Îçî Î°úÎìúÌï† Îç∞Ïù¥ÌÑ∞Í∞Ä ÎÇ®ÏïÑÏûàÏùÑ Îïå
          if (target.isIntersecting && !loading && hasMoreRef.current) {
            setPage(prevPage => prevPage + 1);
          }
        },
        {
          root: null,
          rootMargin: '20px',
          threshold: 1.0,
        }
    );

    if (loaderRef.current) {
      observer.observe(loaderRef.current);
    }

    return () => {
      if (loaderRef.current) {
        observer.unobserve(loaderRef.current);
      }
    };
  }, [loading]); // loading ÏÉÅÌÉúÍ∞Ä Î≥ÄÍ≤ΩÎê† ÎïåÎßå Observer Ïû¨ÏÑ§Ï†ï


  // --- 3. page ÏÉÅÌÉú Î≥ÄÌôî Í∞êÏßÄ Î∞è Ï∂îÍ∞Ä Î°úÎî© (Ïú†ÏßÄ) ---
  useEffect(() => {
    if (page > 0) {
      loadPosts(page, postType);
    }
  }, [page, postType, loadPosts]);


  // --- 4. ‚úÖ [ÏàòÏ†ï] Î†åÎçîÎßÅ Í≤∞Í≥ºÏóê loaderRef Ï∂îÍ∞Ä ---
  return (
      <div className={styles.postListContainer}>
        {posts.map(post => (
            <PostCard key={post.postId} post={post} />
        ))}

        {/* ‚úÖ Î¨¥Ìïú Ïä§ÌÅ¨Î°§Ïùò ÎÅùÏùÑ Í∞êÏßÄÌï† Î°úÎçî ÏöîÏÜå Ï∂îÍ∞Ä */}
        {hasMore && (
            <div ref={loaderRef} className={styles.loader}>
              {loading && <p>Î°úÎî© Ï§ë...</p>}
            </div>
        )}

        {/* Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÍ≥† Î°úÎî©Ïù¥ ÎÅùÎÇ¨ÏùÑ Îïå Î©îÏãúÏßÄ */}
        {!hasMore && posts.length === 0 && !loading && (
            <p className={styles.noPosts}>ÌëúÏãúÌï† Í≤åÏãúÎ¨ºÏù¥ ÏóÜÏäµÎãàÎã§.</p>
        )}
      </div>
  );
}