export async function getPostTags(postId: number): Promise<string[]> {
  const url = `${BLOGSERVICE_API}/tags?postId=${postId}`;

  const response = await fetch(url, {
    method: "GET",
  });

  if (response.ok) {
    return await response.json();
  } else {
    const errorText = await response.text().catch(() => " ")
    const errorMessage = errorText || `ÌÉúÍ∑∏ Ï°∞Ìöå Ïã§Ìå®: HTTP ${response.status} ÏùëÎãµ`;
    throw new Error(errorMessage);
  }
}

export async function uploadImage(file: File): Promise<{ url: string }> {
  const url = `${BLOGSERVICE_API}/upload`;

  const formData = new FormData();
  formData.append("file", file); // Î∞±ÏóîÎìú @RequestParam("file")Í≥º Ïù¥Î¶Ñ ÏùºÏπò

  const response = await fetch(url, {
    method: "POST",
    body: formData, // ÌååÏùº ÏóÖÎ°úÎìú Ïãú Content-TypeÏùÄ ÏÑ§Ï†ïÌïòÏßÄ ÏïäÏäµÎãàÎã§ (Î∏åÎùºÏö∞Ï†ÄÍ∞Ä ÏûêÎèôÏúºÎ°ú multipart/form-data ÏÑ§Ï†ï)
  });

  if (response.ok) {
    // Î∞±ÏóîÎìúÏóêÏÑú { "url": "..." } ÌòïÌÉúÏùò JSONÏùÑ Î∞òÌôò
    return await response.json();
  } else {
    const errorText = await response.text();
    throw new Error(errorText || `Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïã§Ìå®: HTTP ${response.status}`);
  }
}

export async function toggleLike(postId: number, userSignId: string): Promise<LikeToggleResponseDTO> {
  const url = `${BLOGSERVICE_API}/likes/${postId}`;

  try {
    const response = await fetch(url, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "userSignId": userSignId, // Ìó§ÎçîÏóê userSignId Ìè¨Ìï®
      },
    });

    if (response.ok) {
      return await response.json(); // { isLiked: true, likeCount: 10 } Î∞òÌôò
    } else {
      const errorText = await response.text();
      throw new Error(errorText || `Ï¢ãÏïÑÏöî Ï≤òÎ¶¨ Ïã§Ìå®: HTTP ${response.status}`);
    }
  } catch (error) {
    console.error("Ï¢ãÏïÑÏöî API Ïò§Î•ò:", error);
    throw error;
  }
}

// ‚úÖ [ÏàòÏ†ï] DTOÎ•º ÏßÅÏ†ë Î∞õÏïÑ JSON Î≥∏Î¨∏ÏúºÎ°ú Ï†ÑÏÜ°ÌïòÎèÑÎ°ù Î≥ÄÍ≤Ω
export async function writeFeed(postData: PostCreationRequestDTO, userSignId: string): Promise<PostEntity> {
  console.log("Í∏ÄÏì∞Í∏∞ Î©îÏÑúÎìú Ïã§Ìñâ (JSON DTO Î∞©Ïãù)");
  const url = `${BLOGSERVICE_API}/write`;

  const response = await fetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "userSignId": userSignId,
    },
    body: JSON.stringify(postData), // DTOÎ•º JSON Î≥∏Î¨∏ÏúºÎ°ú Ï†ÑÏÜ°
  });

  if (response.ok) {
    return await response.json(); // PostEntity Î∞òÌôò
  } else {
    const errorText = await response.text();
    throw new Error(errorText || `Í≤åÏãúÍ∏Ä Îì±Î°ù Ïã§Ìå®: HTTP ${response.status}`);
  }
}


