// ‚úÖ [ÏàòÏ†ï] updatePostÏùò userSignId Ïù∏Ïûê ÏúÑÏπòÎ•º ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ ÏΩîÎìúÏôÄ ÏùºÏπò
export async function updatePost(postId: number, postData: PostCreationRequestDTO, userSignId: string): Promise<PostEntity> {
  const url = `${BLOGSERVICE_API}/write/${postId}`;

  const response = await fetch(url, {
    method: "POST",
    headers: {
      "Content-Type": "application/json",
      "userSignId": userSignId, // Ìó§ÎçîÏóê userSignId Ìè¨Ìï®
    },
    body: JSON.stringify(postData),
  });

  if (response.ok) {
    return await response.json();
  } else {
    const errorText = await response.text();
    throw new Error(errorText || `Í≤åÏãúÍ∏Ä ÏàòÏ†ï Ïã§Ìå®: HTTP ${response.status}`);
  }
}

export async function deleteFeed(postId: number, userSignId: string): Promise<void> {
  const url = `${BLOGSERVICE_API}/${postId}`;

  const response = await fetch(url, {
    method: "DELETE",
    headers: {
      "userSignId": userSignId, // Ìó§ÎçîÏóê userSignId Ìè¨Ìï®
    },
  });

  if (!response.ok) {
    const errorText = await response.text();
    throw new Error(errorText || `Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú Ïã§Ìå®: HTTP ${response.status}`);
  }
}

