// components/FriendActionModal.tsx
import React, { useEffect, useState } from "react";
import "../../styles/blogService/moduleDeleteConfirmModal.css";
import {
  getFriendshipStatus,
  sendFriendRequest,
  acceptFriendRequest,
  rejectFriendRequest,
  deleteFriend,
  blockUser,
  unblockUser,
} from "@/api/userService/friends";
import type { FriendsResDTO } from "@/types/userService/friends";

interface FriendActionModalProps {
  currentUserSignId: string;        // Î°úÍ∑∏Ïù∏Ìïú ÏÇ¨Ïö©Ïûê
  targetUserSignId: string;
  targetUserNickname: string;
  isOpen: boolean;
  onClose: () => void;
  onSuccess?: () => void;
}

export default function FriendActionModal({
                                            currentUserSignId,
                                            targetUserSignId,
                                            targetUserNickname,
                                            isOpen,
                                            onClose,
                                            onSuccess,
                                          }: FriendActionModalProps) {
  const [status, setStatus] = useState<FriendsResDTO | null>(null);
  const [loading, setLoading] = useState(false);

  const fetchStatus = async () => {
    if (!isOpen) return;
    setLoading(true);
    try {
      const data = await getFriendshipStatus(currentUserSignId, targetUserSignId);
      setStatus(data);
    } catch (err) {
      setStatus(null);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (isOpen) fetchStatus();
  }, [isOpen, targetUserSignId]);

  const handle = async (action: () => Promise<any>) => {
    setLoading(true);
    try {
      await action();
      await fetchStatus();
      onSuccess?.();
    } catch (err: any) {
      alert(err.message || "ÏûëÏóÖ Ïã§Ìå®");
    } finally {
      setLoading(false);
    }
  };

  if (!isOpen) return null;

  const isMeBlocked = status?.friendsStatus === "BLOCKED" && status.requesterSignId === currentUserSignId;
  const isPendingFromMe = status?.friendsStatus === "PENDING" && status.requesterSignId === currentUserSignId;
  const isPendingToMe = status?.friendsStatus === "PENDING" && status.receiverSignId === currentUserSignId;
  const isFriend = status?.friendsStatus === "ACCEPTED";

  return (
      <div className="modalBackdrop" onClick={onClose}>
        <div className="modalContent" onClick={(e) => e.stopPropagation()}>
          <h2 className="modalTitle">
            {isFriend ? "ÏπúÍµ¨" : isMeBlocked ? "Ï∞®Îã®Îê®" : isPendingToMe ? "ÏπúÍµ¨ ÏöîÏ≤≠" : "ÏπúÍµ¨ Ï∂îÍ∞Ä"}
          </h2>

          {loading && !status ? (
              <p className="modalMessage">Î∂àÎü¨Ïò§Îäî Ï§ë...</p>
          ) : (
              <>
                {/* Ï∞®Îã®Îê® */}
                {isMeBlocked && (
                    <>
                      <p className="modalMessage">
                        <strong>{targetUserNickname}</strong>ÎãòÏùÑ Ï∞®Îã®ÌñàÏäµÎãàÎã§.
                      </p>
                      <div className="modalActions">
                        <button className="cancelButton" onClick={onClose}>Îã´Í∏∞</button>
                        <button
                            className="deleteButton"
                            disabled={loading}
                            onClick={() => handle(() => unblockUser(currentUserSignId, status!.friendId))}
                        >
                          {loading ? "Ìï¥Ï†ú Ï§ë..." : "Ï∞®Îã® Ìï¥Ï†ú"}
                        </button>
                      </div>
                    </>
                )}

                {/* ÎÇ¥Í∞Ä Î∞õÏùÄ ÏöîÏ≤≠ */}
                {isPendingToMe && (
                    <>
                      <p className="modalMessage">
                        <strong>{targetUserNickname}</strong>ÎãòÏù¥ ÏπúÍµ¨ ÏöîÏ≤≠ÏùÑ Î≥¥ÎÉàÏäµÎãàÎã§.
                      </p>
                      <div className="modalActions">
                        <button
                            className="cancelButton"
                            disabled={loading}
                            onClick={() => handle(() => rejectFriendRequest(currentUserSignId, status!.friendId))}
                        >
                          Í±∞Ï†àÌïòÍ∏∞
                        </button>
                        <button
                            className="deleteButton"
                            disabled={loading}
                            onClick={() => handle(() => acceptFriendRequest(currentUserSignId, status!.friendId))}
                        >
                          ÏàòÎùΩÌïòÍ∏∞
                        </button>
                      </div>
                    </>
                )}

                {/* ÎÇ¥Í∞Ä Î≥¥ÎÇ∏ ÏöîÏ≤≠ */}
                {isPendingFromMe && (
                    <>
                      <p className="modalMessage">
                        <strong>{targetUserNickname}</strong>ÎãòÏóêÍ≤å ÏπúÍµ¨ ÏöîÏ≤≠ÏùÑ Î≥¥ÎÉàÏäµÎãàÎã§.
                      </p>
                      <div className="modalActions">
                        <button className="cancelButton" onClick={onClose}>Îã´Í∏∞</button>
                      </div>
                    </>
                )}

                {/* ÏπúÍµ¨ ÏÉÅÌÉú */}
                {isFriend && (
                    <>
                      <p className="modalMessage">
                        <strong>{targetUserNickname}</strong>ÎãòÍ≥º ÏπúÍµ¨ÏûÖÎãàÎã§.
                      </p>
                      <div className="modalActions">
                        <button
                            className="cancelButton"
                            disabled={loading}
                            onClick={() => handle(() => deleteFriend(currentUserSignId, status!.friendId))}
                        >
                          ÏπúÍµ¨ ÎÅäÍ∏∞
                        </button>
                        <button
                            className="deleteButton"
                            disabled={loading}
                            onClick={() => handle(() => blockUser(currentUserSignId, targetUserSignId))}
                        >
                          Ï∞®Îã®ÌïòÍ∏∞
                        </button>
                      </div>
                    </>
                )}

                {/* Í¥ÄÍ≥Ñ ÏóÜÏùå */}
                {!status && (
                    <>
                      <p className="modalMessage">
                        <strong>{targetUserNickname}</strong>ÎãòÏóêÍ≤å<br />ÏπúÍµ¨ ÏöîÏ≤≠ÏùÑ Î≥¥ÎÇ¥ÏãúÍ≤†ÏäµÎãàÍπå?
                      </p>
                      <div className="modalActions">
                        <button className="cancelButton" onClick={onClose}>Ï∑®ÏÜå</button>
                        <button
                            className="deleteButton"
                            disabled={loading}
                            onClick={() => handle(() => sendFriendRequest(currentUserSignId, targetUserSignId))}
                        >
                          {loading ? "Î≥¥ÎÇ¥Îäî Ï§ë..." : "ÏπúÍµ¨ ÏöîÏ≤≠"}
                        </button>
                      </div>
                    </>
                )}
              </>
          )}
        </div>
      </div>
  );
}